import React from 'react';
import './registerstyle.css';
import Logo from "./logovaulltismall.png";
import { Image } from "./RegisterElements";

class Register extends React.Component {
    constructor() {
      super();
      this.state = {
        fields: {},
        errors: {}
      }

      this.handleChange = this.handleChange.bind(this);
      this.submituserRegistrationForm = this.submituserRegistrationForm.bind(this);

    };

    handleChange(e) {
      let fields = this.state.fields;
      fields[e.target.name] = e.target.value;
      this.setState({
        fields
      });

    }

    submituserRegistrationForm(e) {
      e.preventDefault();
      if (this.validateForm()) {
          let fields = {};
          fields["username"] = "";
          fields["email"] = "";
          fields["first name"] = "";
          fields["last name"] = "";
          fields["company"] = "";
          fields["password"] = "";
          this.setState({fields:fields});
          alert("Form submitted");
      }

    }

    validateForm() {

      let fields = this.state.fields;
      let errors = {};
      let formIsValid = true;

      if (!fields["username"]) {
        formIsValid = false;
        errors["username"] = "*Please enter your username.";
      }

      if (typeof fields["username"] !== "undefined") {
        if (!fields["username"].match(/^[a-zA-Z ]*$/)) {
          formIsValid = false;
          errors["username"] = "*Please enter alphabet characters only.";
        }
      }

      if (!fields["email"]) {
        formIsValid = false;
        errors["email"] = "*Please enter your email.";
      }

      if (typeof fields["email"] !== "undefined") {
        //regular expression for email validation
        var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern.test(fields["email"])) {
          formIsValid = false;
          errors["email"] = "*Please enter valid email.";
        }
      }

      if (!fields["first name"]) {
        formIsValid = false;
        errors["first name"] = "*Please enter your first name.";
      }

      if (typeof fields["first name"] !== "undefined") {
        if (!fields["first name"].match(/^[a-zA-Z ]*$/)) {
          formIsValid = false;
          errors["first name"] = "*Please enter alphabet characters only.";
        }
      }

    if (!fields["last name"]) {
      formIsValid = false;
      errors["last name"] = "*Please enter your last name.";
    }

    if (typeof fields["last name"] !== "undefined") {
      if (!fields["last name"].match(/^[a-zA-Z ]*$/)) {
        formIsValid = false;
        errors["last name"] = "*Please enter alphabet characters only.";
      }
    }

    if (!fields["company"]) {
      formIsValid = false;
      errors["company"] = "*Please enter your company.";
    }

    if (typeof fields["company"] !== "undefined") {
      //regular expression for email validation
      var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
      if (!pattern.test(fields["company"])) {
        formIsValid = false;
        errors["company"] = "*Please enter valid company.";
      }
    }
      
      if (!fields["password"]) {
        formIsValid = false;
        errors["password"] = "*Please enter your password.";
      }

      if (typeof fields["password"] !== "undefined") {
        if (!fields["password"].match(/^.*(?=.{8,})(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%&]).*$/)) {
          formIsValid = false;
          errors["password"] = "*Please enter secure and strong password.";
        }
      }

      this.setState({
        errors: errors
      });
      return formIsValid;


    }



  render() {
    return (
    <div id="main-registration-container">
     <div id="register">
        <h3 justify='center'>new user registration</h3>
        <form method="post"  name="userRegistrationForm"  onSubmit= {this.submituserRegistrationForm} >
        <label>Username</label>
        <input type="text" name="username" value={this.state.fields.username} onChange={this.handleChange} />
        <div className="errorMsg">{this.state.errors.username}</div>
        <label>Email:</label>
        <input type="text" name="email" value={this.state.fields.email} onChange={this.handleChange}  />
        <div className="errorMsg">{this.state.errors.email}</div>
        <label>First Name:</label>
        <input type="text" name="first name" value={this.state.fields.firstname} onChange={this.handleChange}   />
        <div className="errorMsg">{this.state.errors.firstname}</div>
        <label>Last Name:</label>
        <input type="text" name="last name" value={this.state.fields.lastname} onChange={this.handleChange}   />
        <div className="errorMsg">{this.state.errors.lastname}</div>
        <label>Company:</label>
        <input type="text" name="company" value={this.state.fields.company} onChange={this.handleChange}   />
        <div className="errorMsg">{this.state.errors.company}</div>
        <label>Password</label>
        <input type="password" name="password" value={this.state.fields.password} onChange={this.handleChange} />
        <div className="errorMsg">{this.state.errors.password}</div>
        <input type="submit" className="button"  value="Register"/>
        </form>
    </div>
    <div>  <Image>
  <img alt="vaullti logo" style= {{width: 330, height: 330}} 
  src={Logo}></img>
  </Image></div>

  <h3 style={{textAlignVertical: "center",textAlign: "center",}}>vaullti llc</h3>

</div>


      );
  }


}


export default Register;

